import Img from 'gatsby-image'
import React from 'react'
import Emoji from 'react-emoji-render'
import styled from 'react-emotion'
import { borders, colors, shadows, transitions } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
}

const StyledSmallFeature = styled.div`
  border-radius: ${borders.radius.normal};
  min-width: 280px;
  max-width: 350px;
  height: 100%;
  text-align: center;
  border: 1px solid ${borders.color.normal};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: ${props => (props.clickable ? 'pointer' : 'default')};
  overflow: hidden;
  padding: 20px;
  margin: auto;

  .title {
    color: ${colors.text.light2};
    font-size: 1.1rem;
  }

  h4 {
    font-weight: 300;
    font-size: 1.3rem;
    line-height: 1.8rem;
  }
  .emoji {
    font-size: 3rem;
  }

  p {
    font-size: 1.2rem;
    color: #444;
  }
`

const SmallFeature = ({ feature, ...props }) => (
  <StyledSmallFeature clickable={!!feature.link} {...props}>
    <div className="title">
      <Emoji className="emoji" text={feature.emoji} />
      <h4>{feature.title}</h4>
    </div>
  </StyledSmallFeature>
)

export default SmallFeature
export { StyledSmallFeature }



