import axios from 'axios'
import Cleave from 'cleave.js/react'
import 'cleave.js/dist/addons/cleave-phone.fr'
import React from 'react'
import styled from 'react-emotion'
import PulseLoader, { StyledPulseLoader } from '../components/PulseLoader'
import SuccessFormComponent from '../components/SuccessFormComponent'
import { BACKEND_FORM_ENDPOINT } from '../constants'
import { borders, colors, shadows, transitions } from '../theme'
import { validateField, getAllUrlParams, isEmpty } from '../util'

const minWait = 300

const StyledSimpleForm = styled.div`
  position: relative;
  max-width: 400px;
  min-width: 300px;
  flex-basis: 400px;
  margin: auto;
  padding: 25px;
  color: ${colors.text.normal};
  background: #fff;
  border-radius: ${borders.radius.normal};
  border: 2px solid ${colors.brand.normal};
  box-shadow: ${shadows.both};
  overflow: hidden;

  .form-label {
    margin: auto;
    margin-bottom: 20px;
    font-size: 1.3rem;
    text-transform: none;
    text-align: center;
    font-weight: normal;
  }

  .input-group {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 5px;
  }
  label {
    font-size: 0.95rem;
    font-weight: 700;
    color: ${colors.text.light};
    display: inline-block;
  }
  .hint {
    color: ${colors.brand.normal};
    font-size: 0.85rem;
    margin-left: 5px;
    opacity: 0;
    transition: ${transitions.all};
    &.active {
      opacity: 1;
    }
  }
  input {
    display: block;
    padding-left: 10px;
    border: none;
    box-sizing: border-box;
    padding: 8px;
    padding-right: 34px;
    border: 1px solid ${borders.color.normal};
    width: 100%;
    border-radius: 4px;
    margin-bottom: 5px;
    outline: none;
    transition: ${transitions.all};
    &:focus {
      border-color: ${colors.secondary.light};
    }
    &::placeholder {
      font-size: 12px;
      color: ${colors.text.light2};
    }
  }
  .input-validation {
    &::after {
      display: block;
      position: absolute;
      bottom: 13px;
      right: 7px;
    }
    &.invalid::after {
      content: '🤔';
    }
    &.valid::after {
      content: '✅';
    }
  }
  button {
    display: block;
    margin: auto;
    text-align: center;
    cursor: pointer;
    margin-top: 30px;
    background: ${colors.brand.normal};
    background-image: linear-gradient(270deg, #fb3253 0, #fc4c4c 100%);
    border: none;
    border-radius: ${borders.radius.small};
    padding: 10px 25px;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    font-weight: 900;
    color: ${colors.brand.inverted};
    box-shadow: ${shadows.both};
    transition: border-radius ${minWait - minWait * 1 / 3}ms linear,
      transform ${minWait}ms ease-in-out;
    outline: none;
    width: 100%;

    &:hover {
      box-shadow: ${shadows.bothHigh};
    }

    &[disabled] {
      box-shadow: none;
      cursor: default;
    }

    &.scaled {
      transform: scale3d(10, 20, 1);
      border-radius: 100%;
    }
  }

  .error {
    color: ${colors.brand.normal};
    text-align: center;
    margin-bottom: -24px;
  }

  ${StyledPulseLoader} {
    transition: opacity 50ms ease-in-out;
    &.hidden {
      opacity: 0;
    }
  }

  @media (max-width: 768px) {
    input {
      height: 48px;
      font-size: 1.2rem;
    }
  }
`

const initialField = {
  touched: false,
  value: '',
  hint: null,
  valid: false,
}

class SimpleForm extends React.Component {
  state = {
    answers: {
      email: initialField,
      phone: initialField,
      company: initialField,
      name: initialField,
    },
    submitting: false,
    submitted: false,
    error: false,
  }

  handleInputBlur(event) {
    const name = event.target.name
    const value = event.target.value
    const hint = validateField(name, value, { checkRequired: false })
    const touched = !isEmpty(value)

    this.setState({
      answers: {
        ...this.state.answers,
        [name]: {
          touched,
          value,
          hint,
          valid: !hint && touched,
        },
      },
    })
  }

  handleInputChange(event) {
    const name = event.target.name
    const value = event.target.value
    const touched = this.state.answers[name].touched && !isEmpty(value)
    let hint

    if (this.state.answers[name].touched) {
      hint = validateField(name, value, { checkRequired: false })
    }

    this.setState({
      answers: {
        ...this.state.answers,
        [name]: {
          touched,
          value,
          hint,
          valid: !hint && touched,
        },
      },
    })
  }

  handleInputInvalid(event) {
    event.preventDefault(event)
    const name = event.target.name
    const value = event.target.value
    const hint = validateField(name, value)
    const touched = !isEmpty(value)

    this.setState({
      answers: {
        ...this.state.answers,
        [name]: {
          touched,
          value,
          hint,
          valid: !hint && touched,
        },
      },
    })
  }

  handleSubmit(event) {
    event.preventDefault()
    const answers = {}
    for (let input in this.state.answers) {
      if (this.state.answers[input].hint) return
      answers[input] = this.state.answers[input].value
    }

    this.setState({ submitting: true })

    const data = { ...answers, ...getAllUrlParams(), submittedOn: new Date() }

    axios
      .get(BACKEND_FORM_ENDPOINT, { params: data })
      .then(response => {
        setTimeout(
          () => this.setState({ submitting: false, submitted: true }),
          1500,
        )
        if (window.analytics) {
          window.analytics.track('lead-form')
        }
      })
      .catch(error => {
        setTimeout(
          () => this.setState({ submitting: false, error: true }),
          1500,
        )
      })
  }

  render() {
    return (
      <StyledSimpleForm {...this.props}>
        <form onSubmit={e => this.handleSubmit(e)}>
          <h3 className="form-label">
            {this.props.formLabel || `Votre réponse en moins de 3 heures\u00A0!`}
          </h3>
          <div className="input-group">
            <label htmlFor="email">
              Email
              <span
                className={`hint ${
                  this.state.answers.email.hint ? `active` : ``
                }`}
              >
                {this.state.answers.email.hint}
              </span>
            </label>
            <input
              required
              id="email"
              name="email"
              type="text"
              placeholder="votre.adresse@entreprise.com"
              value={this.state.answers.email.value}
              onChange={e => this.handleInputChange(e)}
              onBlur={e => this.handleInputBlur(e)}
              onInvalid={e => this.handleInputInvalid(e)}
            />
            <span
              className={`input-validation ${
                this.state.answers.email.hint
                  ? `invalid`
                  : this.state.answers.email.valid
                    ? `valid`
                    : ``
              }`}
            />
          </div>

          <div className="input-group">
            <label htmlFor="phone">
              Téléphone
              <span
                className={`hint ${
                  this.state.answers.phone.hint ? `active` : ``
                }`}
              >
                {this.state.answers.phone.hint}
              </span>
            </label>
            <Cleave
              options={{ phone: true, phoneRegionCode: 'FR' }}
              required
              id="phone"
              name="phone"
              type="tel"
              placeholder="Portable de préférence"
              value={this.state.answers.phone.value}
              onChange={e => this.handleInputChange(e)}
              onBlur={e => this.handleInputBlur(e)}
              onInvalid={e => this.handleInputInvalid(e)}
            />
            <span
              className={`input-validation ${
                this.state.answers.phone.hint
                  ? `invalid`
                  : this.state.answers.phone.valid
                    ? `valid`
                    : ``
              }`}
            />
          </div>

          <div className="input-group">
            <label htmlFor="company">
              Société
              <span
                className={`hint ${
                  this.state.answers.company.hint ? `active` : ``
                }`}
              >
                {this.state.answers.company.hint}
              </span>
            </label>
            <input
              required
              id="company"
              name="company"
              type="text"
              placeholder="Ex: Blablacar"
              value={this.state.answers.company.value}
              onChange={e => this.handleInputChange(e)}
              onBlur={e => this.handleInputBlur(e)}
              onInvalid={e => this.handleInputInvalid(e)}
            />
            <span
              className={`input-validation ${
                this.state.answers.company.hint
                  ? `invalid`
                  : this.state.answers.company.valid
                    ? `valid`
                    : ``
              }`}
            />
          </div>

          <div className="input-group">
            <label htmlFor="name">
              Nom complet
              <span
                className={`hint ${
                  this.state.answers.name.hint ? `active` : ``
                }`}
              >
                {this.state.answers.name.hint}
              </span>
            </label>
            <input
              required
              id="name"
              name="name"
              type="text"
              placeholder="Ex: Jeanne Gaudez"
              value={this.state.answers.name.value}
              onChange={e => this.handleInputChange(e)}
              onBlur={e => this.handleInputBlur(e)}
              onInvalid={e => this.handleInputInvalid(e)}
            />
            <span
              className={`input-validation ${
                this.state.answers.name.hint
                  ? `invalid`
                  : this.state.answers.name.valid
                    ? `valid`
                    : ``
              }`}
            />
          </div>

          {this.state.error ? (
            <p className="error">Ooops une erreur s'est produite...</p>
          ) : null}

          <button
            type="submit"
            disabled={this.state.submitting || this.state.submitted}
            className={this.state.submitted ? `scaled` : ``}
          >
            {this.state.submitting || this.state.submitted ? (
              <PulseLoader className={this.state.submitted ? `hidden` : ``} />
            ) : this.state.error ? (
              `Ressayer`
            ) : (
              this.props.submitLabel || `Obtenez votre devis`
            )}
          </button>
        </form>
        {this.state.submitted ? <SuccessFormComponent wait={minWait} /> : null}
      </StyledSimpleForm>
    )
  }
}

export default SimpleForm
export { StyledSimpleForm }



