import Img from 'gatsby-image'
import React from 'react'
import styled from 'react-emotion'
import { colors } from '../theme'
import SimpleForm from './SimpleForm'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
  zIndex: -1,
  backgroundColor: '#422f30',
}

const StyledMainBlock = styled.div`
  position: relative;
  margin-top: 63px;
  height: 80%;

  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
  }

  .main-container {
    color: white;
    position: relative;
    display: flex;
    max-width: 1230px;
    margin: auto;
    padding: 80px 0;

    .text {
      text-align: center;
      flex: 5;
      align-self: center;
      padding: 50px;
      text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
      .title {
        font-weight: 700;
        font-size: 2.2em;
        line-height: 1.3em;
        /* letter-spacing: 2px; */
        text-transform: uppercase;
      }
      .red-bar {
        height: 5px;
        width: 20%;
        border-radius: 20px 20px;
        background: ${colors.brand.normal};
        background-image: linear-gradient(270deg, #fb3253 0, #fc4c4c 100%);
        margin: 5px auto;
      }
      .subtitle {
        margin: auto;
        max-width: 600px;
        font-weight: 300;
        font-size: 1.4em;
        line-height: 1.4em;
      }
      .button,
      .input {
        width: 320px;
        height: 50px;
        border-radius: 5px;
        font-family: 'Titillium Web', sans-serif;
        box-sizing: border-box;
        outline: none;
        border: none;
        font-weight: bold;
      }
      .button {
        text-align: center;
        background: #fc4c4c;
        margin-top: 15px;
        color: white;
        font-size: 1.05em;
        margin-bottom: 10px;
        transition: 0.3s all;
        cursor: pointer;
        &:hover {
          background: #c52626;
        }
        &:active {
          background: black;
        }
      }
      .input {
        padding: 0px 20px;
        margin-top: 70px;
      }
      .call-me {
        font-style: italic;
        transition: 0.3s all;
        cursor: pointer;
        &:hover {
          text-decoration: underline;
        }
      }
    }

    .form-area {
      flex-grow: 1;
      align-self: center;
      margin-right: 10px;
      min-width: 350px;
    }
  }
  @media screen and (max-width: 768px) {
    background-attachment: initial;
    min-height: 400px;
    height: auto;

    .main-container {
      flex-direction: column-reverse;
      padding: 0;

      .form-area {
        margin: 5px auto;
        min-width: auto;
      }
      .text {
        width: 90%;
        padding: 30px 0;
        margin: auto;
        .title {
          font-size: 1.5em;
        }
        .subtitle {
          font-size: 1em;
        }
        .button,
        .input {
          height: 40px;
          width: 100%;
          max-width: 300px;
        }
        .button,
        .call-me {
          font-size: 0.9em;
        }
      }
    }
  }
`

const MainBlock = ({ page }) => (
  <StyledMainBlock>
    <Img
      sizes={page.mainBackground.sizes}
      alt={page.mainBackground.description}
      style={imgStyle}
    />
    <div className="overlay" />
    <div className="main-container">
      <div className="text">
        <h1 className="title">{page.mainTitle}</h1>
        <div className="red-bar" />
        <h2 className="subtitle">{page.mainSubtitle}</h2>
      </div>
      <div className="form-area">
        <SimpleForm
          formLabel={page.formLabel}
          submitLabel={page.formButtonLabel}
        />
      </div>
    </div>
  </StyledMainBlock>
)

export default MainBlock



