import React from 'react'
import styled from 'react-emotion'
import Helmet from 'react-helmet'
import BanquetForAll from '../components/BanquetForAll'
import CompanyTileList, {
  StyledCompanyTileList,
} from '../components/CompanyTileList'
import ContentBlock from '../components/ContentBlock'
import FeatureList from '../components/FeatureList'
import MainBlock from '../components/MainBlock'
import MissionBlock from '../components/MissionBlock'
import Navbar from '../components/Navbar'
import SimpleFooter from '../components/SimpleFooter'

const StyledMomentPage = styled.div`
  ${StyledCompanyTileList} {
    margin: 80px 0;
  }
`

class MomentPage extends React.Component {
  page = this.props.data.page

  render() {
    return (
      <StyledMomentPage>
        <Helmet>
          <title>{this.page.seoTitle}</title>
          <meta name="description" content={this.page.seoDescription} />
          <meta name="keywords" content={this.page.seoKeywords} />
        </Helmet>
        <Navbar noCta={true} noLink={true} />
        <MainBlock page={this.page} />
        <FeatureList featureList={this.page.featureList} />
        <ContentBlock page={this.page} />
        <CompanyTileList companyList={this.page.companyReferences} />
        <BanquetForAll
          buttonLabel={this.page.bottomCta}
          onClick={() =>
            window.scroll({
              top: 0,
              behavior: 'smooth',
            })
          }
        />
        <MissionBlock page={this.page} />
        <SimpleFooter />
      </StyledMomentPage>
    )
  }
}

export default MomentPage

export const query = graphql`
  query MomentPageQuery($id: String!) {
    page: contentfulMomentPage(id: { eq: $id }) {
      seoTitle
      seoDescription {
        seoDescription
      }
      seoKeywords {
        seoKeywords
      }
      mainTitle
      mainSubtitle
      mainBackground {
        description
        sizes {
          ...GatsbyContentfulSizes_withWebp
        }
      }
      featureList {
        title
        subTitle
        features {
          id
          title
          emoji
          buttonText
          link
          picture {
            title
            sizes(maxWidth: 350) {
              ...GatsbyContentfulSizes_withWebp
            }
          }
        }
      }
      eventName
      eventPicture1 {
        description
        sizes(maxWidth: 1000) {
          ...GatsbyContentfulSizes_withWebp
        }
      }
      eventPicture2 {
        description
        sizes(maxWidth: 1000) {
          ...GatsbyContentfulSizes_withWebp
        }
      }
      company {
        name
      }
      testimonial {
        content {
          content
        }
        author {
          name
          job
          company {
            name
            logo {
              file {
                url
              }
            }
          }
          picture {
            description
            resolutions(width: 80, height: 80) {
              ...GatsbyContentfulResolutions_withWebp
            }
          }
        }
      }
      menu {
        menu
      }
      artisan {
        name
        type
        quote {
          quote
        }
        teamMembers
        picture {
          description
          sizes(maxWidth: 1000) {
            ...GatsbyContentfulSizes_withWebp
          }
        }
      }
      bottomCta
      companyReferences {
        title
        companies {
          id
          name
          logo {
            title
            file {
              url
            }
          }
        }
      }
      missionText {
        missionText
      }
      missionPicture {
        description
        sizes(maxHeight: 450) {
          ...GatsbyContentfulSizes_withWebp
        }
      }
      formLabel
      formButtonLabel
    }
  }
`



