import React from 'react'
import styled from 'react-emotion'
import Feature, { StyledFeature } from '../components/Feature'
import SmallFeature, { StyledSmallFeature } from '../components/SmallFeature'
import Link from '../components/Link'
import { colors, layout } from '../theme'

const StyledFeatureList = styled.section`
  margin: 50px 0;

  h3.section-title {
    text-transform: uppercase;
    color: ${colors.text.light3};
    font-size: 1rem;
    text-align: center;
    padding: 20px;
    font-weight: normal;
  }

  .features {
    display: flex;
    padding: 15px 10px;
    background: ${colors.background.light};
    max-width: ${layout.maxWidth};
    margin: auto;
    justify-content: space-between;

    .feature {
      flex-grow: 1;
      flex-basis: 0;
      margin: 10px;
    }
  }

  @media screen and (max-width: 902px) {
    .features {
      flex-wrap: wrap;
      justify-content: center;
      align-items: stretch;
      padding: 20px 10px;
    }
  }
`

class FeatureList extends React.Component {
  render() {
    return (
      <StyledFeatureList>
        <h3 className="section-title">{this.props.featureList.title}</h3>
        <div className="features">
          {this.props.featureList.features.map(f => (
            <Link to={f.link} key={f.id} className="feature">
              {f.picture && f.buttonText ? (
                <Feature feature={f} />
              ) : (
                <SmallFeature feature={f} />
              )}
            </Link>
          ))}
        </div>
      </StyledFeatureList>
    )
  }
}

export default FeatureList
export { StyledFeatureList }



