import React from 'react'
import styled from 'react-emotion'
import CompanyTile, { StyledCompanyTile } from '../components/CompanyTile'
import { colors } from '../theme'

const StyledCompanyTileList = styled.div`
  margin: 50px 0px;
  margin-bottom: 30px;

  h3.section-title {
    text-transform: uppercase;
    color: ${colors.text.light3};
    font-size: 1rem;
    text-align: center;
    padding: 20px;
    font-weight: normal;
  }
  .companies {
    display: flex;
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;

    ${StyledCompanyTile}:first-child {
      margin-left: auto;
    }

    ${StyledCompanyTile}:last-child {
      margin-right: auto;
    }
  }
`

class CompanyTileList extends React.Component {
  render() {
    return (
      <StyledCompanyTileList>
        <h3 className="section-title">{this.props.companyList.title}</h3>
        <div className="companies">
          {this.props.companyList.companies.map(c => (
            <CompanyTile logo={c.logo} key={c.id} />
          ))}
        </div>
      </StyledCompanyTileList>
    )
  }
}

export default CompanyTileList
export { StyledCompanyTileList }



