import React from 'react'
import styled from 'react-emotion'
import { borders, colors, shadows, transitions } from '../theme'

const StyledSuccessFormComponent = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  padding: 25px;
  color: ${colors.brand.inverted};
  background-image: linear-gradient(270deg, #fb3253 0, #fc4c4c 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: ${transitions.all};
  text-align: center;

  &.hidden {
    opacity: 0;
  }

  h5 {
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 40px;
  }

  i {
    font-size: 3rem;
  }

  p {
    font-size: 1.3rem;
    margin-top: 30px;
  }
`

class SuccessFormComponent extends React.Component {
  state = { hidden: true }

  componentWillMount() {
    setTimeout(() => {
      this.setState({ hidden: false })
    }, this.props.wait)
  }

  render() {
    return (
      <StyledSuccessFormComponent className={this.state.hidden ? `hidden` : ``}>
        <h5>Merci pour votre demande !</h5>
        <i>🎉</i>
        <p>
          Nous revenons vers vous sous 3h pour vous proposer un menu conçu par
          des artisans locaux soigneusement sélectionnés.
        </p>
      </StyledSuccessFormComponent>
    )
  }
}

export default SuccessFormComponent
export { StyledSuccessFormComponent }



