import React from 'react'
import styled from 'react-emotion'
import { borders } from '../theme'

const StyledCompanyTile = styled.div`
  border-radius: ${borders.radius.small};
  min-width: 150px;
  height: 100px;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 10px;
  border: 1px solid #eee;

  .logo {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-height: 40px;
      max-width: 110px;
    }
  }
`

const CompanyTile = ({ logo }) => (
  <StyledCompanyTile>
    <div className="logo">
      <img src={logo.file.url} alt="" />
    </div>
  </StyledCompanyTile>
)

export default CompanyTile
export { StyledCompanyTile }



