import React from 'react'
import styled from 'react-emotion'
import Img from 'gatsby-image'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
  zIndex: -1,
  backgroundColor: '#663930',
}

const StyledMissionBlock = styled.div`
  height: 450px;
  position: relative;
  .text {
    color: white;
    width: 70%;
    max-width: 650px;
    font-size: 1.6em;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.1);
  }
  @media screen and (max-width: 768px) {
    background-attachment: initial;
    .text {
      font-size: 1.1em;
      width: 90%;
    }
  }
`

const MissionBlock = ({ page }) => {
  return (
    <StyledMissionBlock>
      <Img
        sizes={page.missionPicture.sizes}
        alt={page.missionPicture.description}
        style={imgStyle}
      />
      <div className="text">{page.missionText.missionText}</div>
    </StyledMissionBlock>
  )
}

export default MissionBlock



