import Img from 'gatsby-image'
import React from 'react'
import Emoji from 'react-emoji-render'
import styled from 'react-emotion'
import { borders, colors, shadows, transitions } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
}

const StyledFeature = styled.div`
  border-radius: ${borders.radius.normal};
  min-width: 280px;
  max-width: 350px;
  height: 420px;
  text-align: center;
  border: 1px solid ${borders.color.normal};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: ${props => (props.clickable ? 'pointer' : 'default')};
  box-shadow: ${shadows.both};
  overflow: hidden;
  padding: 20px;
  margin: auto;
  transition: ${transitions.all};

  .title {
    color: ${colors.text.light2};
    font-size: 1.1rem;
  }

  h4 {
    font-weight: 300;
    font-size: 1.3rem;
    padding: 0 37px;
    line-height: 1.8rem;
  }
  .emoji {
    font-size: 3rem;
  }

  p {
    font-size: 1.2rem;
    color: #444;
  }

  .buttonText {
    padding: 4px 8px;
    margin: auto;
    margin-bottom: 30px;
    color: ${colors.secondary.light};
    cursor: pointer;
    font-size: 1rem;
    font-weight: 700;
    transition: ${transitions.all};
    background: #fff;
    border: none;
  }

  .picture {
    position: relative;
    width: 100%;
    height: 150px;
    flex-shrink: 0;
    overflow: hidden;
    border-radius: ${borders.radius.small};
  }

  &:hover {
    transform: scale3d(1.005, 1.005, 1);
    box-shadow: ${shadows.bothDiffused};

    .buttonText {
      color: ${colors.secondary.light};
    }
  }
`

const Feature = ({ feature, ...props }) => (
  <StyledFeature clickable={!!feature.link} {...props}>
    <div className="title">
      <Emoji className="emoji" text={feature.emoji} />
      <h4>{feature.title}</h4>
    </div>
    <button className="buttonText">{feature.buttonText} →</button>
    <div className="picture">
      <Img
        sizes={feature.picture.sizes}
        alt={feature.picture.title}
        style={imgStyle}
      />
    </div>
  </StyledFeature>
)

export default Feature
export { StyledFeature }



