import React from 'react'
import styled from 'react-emotion'

const StyledPulseLoader = styled.div`
  @keyframes pulse {
    0% {
      transform: scale3d(1, 1, 1);
      opacity: 1;
    }
    45% {
      transform: scale3d(0.1, 0.1, 0.1);
      opacity: 0.7;
    }
    80% {
      transform: scale3d(1, 1, 1);
      opacity: 1;
    }
  }

  .ball {
    background-color: #fff;
    width: 10px;
    height: 10px;
    display: inline-block;
    margin: 2px;
    border-radius: 100%;
    animation: pulse 0.75s cubic-bezier(0.2, 0.68, 0.18, 1.08) 0.12s infinite
      both;
  }
  .ball-2 {
    animation-delay: 150ms;
  }
  .ball-3 {
    animation-delay: 300ms;
  }
`

const PulseLoader = ({ ...props }) => (
  <StyledPulseLoader {...props}>
    <div className="ball" />
    <div className="ball ball-2" />
    <div className="ball ball-3" />
  </StyledPulseLoader>
)

export default PulseLoader
export { StyledPulseLoader }



