import Img from 'gatsby-image'
import React from 'react'
import styled from 'react-emotion'
import { borders, colors, layout } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
  borderRadius: borders.radius.small,
}

const StyledContentBlock = styled.div`
  max-width: 1110px;
  padding: 0 ${layout.containerPadding.normal};
  margin: auto;
  margin-top: 50px;

  .picture {
    position: relative;
  }

  h3 {
    text-transform: uppercase;
    color: ${colors.text.light3};
    font-size: 1rem;
    text-align: center;
    padding: 20px;
    font-weight: normal;
  }

  .customer-banner {
    position: relative;
    width: 100%;
    padding: 30px;
  }

  .company-logo {
    position: relative;
    margin: auto;
    width: 200px;
    height: 200px;
    background: #fff;
    border-radius: 100px;
  }

  .testimonial {
    width: 80%;
    margin: auto;
    margin-top: 30px;
    padding: 20px 60px;

    p.quote {
      position: relative;
      font-size: 1.2rem;
      text-align: justify;

      &::before {
        content: '❝';
        color: ${colors.brand.normal};
        font-size: 2.3rem;
        font-family: cursive;
        position: absolute;
        left: -40px;
        top: -15px;
      }
    }

    .author {
      margin-top: 20px;
      display: flex;
      align-items: center;
    }
    .author-details {
      margin-left: auto;
      text-align: right;
      margin-right: 15px;
    }

    .author-picture {
      position: relative;
      width: 80px;
      height: 80px;
      border-radius: 60px;
      overflow: hidden;
    }
  }

  .artisan-title {
    margin-top: 50px;
  }
  .artisan {
    display: flex;
    border: 1px solid ${borders.color.light};
    border-radius: ${borders.radius.small};
    padding: 15px;

    .quote {
      font-size: 1.1rem;
      margin-bottom: 10px;
    }
  }
  .artisan-picture {
    position: relative;
    flex-basis: 55%;
    flex-shrink: 0;
    height: 240px;
  }
  .artisan-quote {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }

  .team,
  .testimonial .name {
    color: ${colors.brand.normal};
  }
  .artisan .name,
  .job {
    font-weight: 900;
    text-transform: uppercase;
  }

  .with-line::before {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    background: linear-gradient(270deg, #fb3253 0, #fc4c4c 100%);
    transform: skewY(-2deg);
    width: 150%;
    left: -50%;
    height: 3px;
    margin-top: -30px;
  }
  .with-line.line-right::before {
    transform: skewY(2deg);
  }

  @media (max-width: 768px) {
    .company-logo {
      width: 150px;
      height: 150px;
    }
    .testimonial {
      margin: 0 auto;
      padding: 20px 10px;
    }
    .artisan {
      flex-direction: column;
    }
    .artisan-quote {
      margin: 15px 0;
    }
  }
`

const ContentBlock = ({ page }) => (
  <StyledContentBlock>
    <h3 className="with-line">{page.eventName}</h3>
    <div className="customer-banner">
      <Img
        sizes={page.eventPicture1.sizes}
        alt={page.eventPicture1.description}
        style={imgStyle}
      />
      <div className="company-logo">
        <img
          src={page.testimonial.author.company.logo.file.url}
          alt={page.testimonial.author.company.logo.title}
          style={{
            ...imgStyle,
            width: 'auto',
            height: 'auto',
            maxWidth: '70%',
            maxHeight: '70%',
            top: '50%',
            left: '50%',
            transform: 'translate3d(-50%,-50%, 0)',
          }}
        />
      </div>
    </div>

    <div className="testimonial">
      <p className="quote">{page.testimonial.content.content}</p>
      <div className="author">
        <div className="author-details">
          <p className="name">{page.testimonial.author.name}</p>
          <p className="job">{page.testimonial.author.job}</p>
        </div>
        <div className="author-picture">
          <Img
            resolutions={page.testimonial.author.picture.resolutions}
            alt={page.testimonial.author.picture.title}
            style={imgStyle}
          />
        </div>
      </div>
    </div>

    {/* <Menu picture={page.eventPicture2}>{page.menu.menu}</Menu> */}

    <h3 className="artisan-title with-line line-right">
      Des artisans partenaires passionnés
    </h3>
    <div className="artisan">
      <div className="artisan-picture">
        <Img
          sizes={page.artisan.picture.sizes}
          alt={page.artisan.picture.description}
          style={imgStyle}
        />
      </div>
      <div className="artisan-quote">
        <p className="quote">{page.artisan.quote.quote}</p>
        <div className="team">{page.artisan.teamMembers}</div>
        <div className="name">{page.artisan.name}</div>
      </div>
    </div>
  </StyledContentBlock>
)

export default ContentBlock



